@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,400;1,400&display=swap);
html{box-sizing:border-box}*{font-family:"Poppins", sans-serif;color:#0c1115;font-weight:bold}*::before,*::after{box-sizing:inherit}body{width:100vw;margin:0;padding:0;color:#202020;background-color:#fafafa}.header{background-color:#051622;width:100%;height:40px;padding:0.5rem}.header nav{max-width:992px;margin:auto;display:flex;justify-content:flex-start;align-items:center}.header nav strong{font-size:medium;color:white;font-weight:500;margin-left:3px}.header div{display:flex;flex-direction:row}.header img{width:45px;height:45px}.content{height:100vh;display:flex;width:100%;flex-direction:row;margin:2rem 5rem}.sidebar{-webkit-user-select:none;-ms-user-select:none;user-select:none;width:25%;height:100vh}.sidebar div{cursor:pointer;margin-bottom:2rem;padding:1rem}.sidebar div .icon{margin-right:1rem}.active{background-color:white}.task-box{border-left:1px solid rgba(128,128,128,0.11);padding:1rem;height:100vh;width:calc(60% - 30%);background-color:white}.task-box #plus{padding:0.5rem;font-size:large;color:#126e68}.task-box .add-task-btn{cursor:pointer}.task-box .input-text{color:rgba(0,0,0,0.253)}.add-task-dialog{width:100%}.add-task-dialog input{width:96%;height:1rem;padding:0.3rem;margin:5px auto;border-radius:3px;border:1px solid #2020204b;outline:none}.add-task-dialog input:focus{outline:1px solid rgba(86,156,248,0.699)}.add-task-dialog .actions{display:flex;flex-direction:row;justify-content:space-between}.add-task-dialog button{font-size:small;cursor:pointer;border-radius:5px;border:none;padding:0.6rem;margin-right:5px}.add-task-dialog button:active{-webkit-transform:scale(0.9);transform:scale(0.9)}.add-task-dialog button:hover{transition:trahsform 0.3s ease-in-out}.add-task-dialog .add-btn{background-color:#1ba098;color:white}.add-task-dialog .add-btn:disabled{background-color:gray}.add-task-dialog .add-btn:active .active{background-color:white}li{list-style-type:none}

